.container {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  background-color: #4caf50;
  color: white;
  padding: 20px;
  text-align: center;
}

.header h1 {
  margin: 0;
  font-size: 2em;
}

.nav-link {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
  margin-top: 10px;
  display: block;
}

.nav-link:hover {
  text-decoration: underline;
}

.main {
  flex: 1 1 auto;
  padding: 20px;
  background-color: #f9f9f9;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.input-group, .search-group {
  margin-bottom: 20px;
}

input[type="text"] {
  padding: 10px;
  margin-right: 10px;
}

button {
  padding: 10px;
  margin-right: 5px;
  cursor: pointer;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 10px;
}

.footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px;
}

.footer h3 {
  margin: 0;
  font-size: 0.9em;
}
